




































































































import {Vue, Component, Watch, Emit} from 'vue-property-decorator'
import CommonDialog from "~/components/common/CommonDialog.vue";
import CommonForm from "~/components/common/CommonForm.vue";
import CommonFormItem from "~/components/common/CommonFormItem.vue";
import UrlItem from './UrlItem.vue'

interface urlFormItem {
  name: string,
  url: string,
  favicon: string
}

@Component({
  components: {CommonFormItem, CommonForm, CommonDialog, UrlItem}
})

export default class UrlList extends Vue {
  $axios: any;
  url_items: object[] = []
  show_add_dialog: boolean = false
  show_array_box: boolean = false
  searching: boolean = false

  add_url_form: urlFormItem = {
    name: '',
    url: '',
    favicon: ''
  }
  icon_url_list: string[] = []
  user_url_list: any = []
  type: string = 'add'
  edit_index: number = 0

  choosed_ind: number | null = null
  moveto_ind: number | null = null
  inner_choosed_ind: number | null = null
  inner_moveto_ind: number | null = null
  group_id: number | null = null // 建立分组
  mouse_status: number = 0

  baseX: number = 0
  baseY: number = 0
  card_offset_X: number = 0
  card_offset_Y: number = 0
  offsetX: number = 0
  offsetY: number = 0

  inner_baseX: number = 0
  inner_baseY: number = 0
  inner_card_offset_X: number = 0
  inner_card_offset_Y: number = 0
  inner_offsetX: number = 0
  inner_offsetY: number = 0

  $store: any;
  timer: any = null

  async created() {

  }

  test_xss() {
    const html = document.querySelector('#test_xss')
    console.log(`html ==> `, html)
    if (html !== null) {
      // html.innerHTML += this.add_url_form.name
      html.innerHTML = `hello vue<img src="../qwe" οnerrοr="alert(1)">`
    }
  }

  async mounted() {
    await this.$store.dispatch('user/get_user_url_list')
    this.user_url_list = this.$store.state.user.user_url_list
  }

  async get_favicon() {
    this.searching = true
    try {
      let res = await this.$axios.get(`/xz_server/search/getUrlFav`, {params: {url: this.add_url_form.url}})
      console.log(`res ==> `, res)
      this.icon_url_list = res.data
    } catch (e) {
      console.log('get_favicon:', e)
    }
    this.searching = false
  }

  jump(url: string) {
    window.open(url)
  }

  add_url() {
    this.type = 'add'
    this.show_add_dialog = true
  }

  close_add_dialog() {
    this.add_url_form = {
      name: '',
      url: '',
      favicon: ''
    }
    this.icon_url_list = []
    this.show_add_dialog = false
  }

  async on_img_load_error(e: { target: { src: any; }; }, i: string | number) {
    console.log(`on_img_load_error e ==> `, e)
    try {
      const res = await this.$axios.post('/xz_server/upload/downLoadImage', {
        url: e.target.src,
        folder: 'favicon'
      })
      if (res.data) {
        this.$set(this.icon_url_list, i, res.data.url)
      }
    } catch (e) {
      console.log(`e ==> `, e)
    }
    console.log(`this.icon_url_list ==> `, this.icon_url_list)

  }

  async choose_favicon(i: number) {
    if (this.icon_url_list[i].startsWith('http://180.76.105.167/upload')) {
      this.add_url_form.favicon = this.icon_url_list[i]
    } else {
      const res = await this.$axios.post('/xz_server/upload/downLoadImage', {
        url: this.icon_url_list[i],
        folder: 'favicon'
      })
      this.add_url_form.favicon = res.data.url
    }
  }

  async upload_url() {
    const res = await this.$axios.post('/xz_server/upload/downLoadImage', {
      url: this.add_url_form.favicon,
      folder: 'favicon'
    })
    this.add_url_form.favicon = res.data.url
  }

  async add_web_url() {

    switch (this.type) {
      case 'add':
        await this.$axios.post('/xz_server/url/addUrl', {url_item: this.add_url_form})
        break
      case 'edit':
        const user_url_list = JSON.parse(JSON.stringify(this.user_url_list))
        if (this.group_id === null) {
          user_url_list[this.edit_index] = this.add_url_form
        } else {
          user_url_list[this.group_id][this.edit_index] = this.add_url_form
        }
        await this.$axios.post('/xz_server/url/setUrlList', {url_list: user_url_list})
        break
    }

    this.add_url_form = {
      name: '',
      url: '',
      favicon: ''
    }
    await this.$store.dispatch('user/get_user_url_list')
    this.user_url_list = this.$store.state.user.user_url_list
    this.close_add_dialog()
  }

  @Emit('edit_item')
  async edit_item(url_item: urlFormItem, i: number) {
    this.type = 'edit'
    this.show_add_dialog = true

    this.add_url_form = {
      name: url_item.name,
      url: url_item.url,
      favicon: url_item.favicon
    }
    this.edit_index = i
  }

  @Emit('rm_item')
  async rm_item(i: number) {
    let user_url_list = JSON.parse(JSON.stringify(this.user_url_list))
    if (this.group_id === null) {
      user_url_list.splice(i, 1)
    } else {
      user_url_list[this.group_id].splice(i, 1)
    }
    await this.$axios.post('/xz_server/url/setUrlList', {url_list: user_url_list})
    await this.$store.dispatch('user/get_user_url_list')
    this.user_url_list = this.$store.state.user.user_url_list
  }

  onMousedown(e: any, ind: number, is_inner: boolean) {
    if (is_inner) {
      this.inner_choosed_ind = ind
      this.inner_baseX = e.pageX
      this.inner_baseY = e.pageY
      this.inner_card_offset_X = e.offsetX
      this.inner_card_offset_Y = e.offsetY
      this.mouse_status = 1
    } else {
      if (this.group_id !== null) {
        return
      } else {
        this.choosed_ind = ind
        this.baseX = e.pageX
        this.baseY = e.pageY
        this.card_offset_X = e.offsetX
        this.card_offset_Y = e.offsetY
        this.mouse_status = 1
      }
    }


  }

  onMousemove(e: any) {
    if (this.mouse_status) {
      if (this.group_id !== null) {
        const box_dom: any = this.$refs.url_item_arr_box
        const box_rect = box_dom.getBoundingClientRect()
        this.inner_offsetX = e.pageX - this.inner_baseX
        this.inner_offsetY = e.pageY - this.inner_baseY
        // todo:移出 box(arr box)
        if (e.clientX < box_rect.x || e.clientX > box_rect.x + box_rect.width
          || e.clientY < box_rect.y || e.clientY > box_rect.y + box_rect.height
        ) {
          if (box_dom.className.indexOf('hide') === -1) {
            box_dom.className += ' hide'
            this.show_array_box = false
          }
          // this.show_array_box = false
          // this.group_id = null
        }
      } else {
        this.offsetX = e.pageX - this.baseX
        this.offsetY = e.pageY - this.baseY
      }

    }
  }

  onMouseleave() {
    // console.log('onMouseleave')
    // if (this.group_id !== null && this.mouse_status) {
    //   this.show_array_box = false
    //   this.group_id = null
    // }
  }

  // todo:item:移入的box
  onMouseenter(e: any, item: any, i: number, is_inner: boolean) {

    if (this.mouse_status) {
      console.log(`item1 ==> `, item)
      if (is_inner) {
        // todo:组内移动
        this.inner_moveto_ind = i

      } else if (this.group_id !== null) {
        // todo:从组中拿出来移动到组外
        const user_url_list = JSON.parse(JSON.stringify(this.user_url_list[this.group_id]))
        let box = user_url_list.splice(this.inner_choosed_ind as number, 1)
        console.log(`box ==> `, box)
        this.choosed_ind = this.group_id
        this.moveto_ind = i
      } else {
        // todo:组外移动
        this.moveto_ind = i
        const user_url_list = JSON.parse(JSON.stringify(this.user_url_list))
        let box = user_url_list.splice(this.choosed_ind as number, 1)

        if (this.timer) {
          clearTimeout(this.timer)
          this.timer = null
        }
        this.timer = setTimeout(async () => {
          if (this.mouse_status) {

            console.log(`item ==> `, item)
            console.log(`box[0] ==> `, box[0])
            let arr
            if (Array.isArray(item)) {
              arr = [...item, box[0]]
            } else {
              arr = [item, box[0]]
            }
            console.log(`user_url_list ==> `, user_url_list)


            if (this.moveto_ind === null || this.choosed_ind === null) {
              return
            } else if (this.moveto_ind < this.choosed_ind) {
              // 往回移动
              user_url_list.splice(this.moveto_ind, 1, arr)
            } else {
              // 往外移动
              user_url_list.splice(this.moveto_ind - 1, 1, arr)
            }

            this.user_url_list = user_url_list

            if (this.moveto_ind < this.choosed_ind) {
              // 往回移动
              this.group_id = this.moveto_ind
            } else {
              // 往外移动
              this.group_id = this.moveto_ind - 1
            }

            setTimeout(() => {
              this.show_array_box = true
            })
            this.cancel_state()
            await this.$axios.post('/xz_server/url/setUrlList', {url_list: user_url_list})
          }
        }, 1000)
      }
    }

  }

  async onMouseup(e: any, item: any, i: number) {
    const box_dom: any = this.$refs.url_item_arr_box
    if (box_dom.className.indexOf('hide') !== -1) {
      box_dom.className = 'url_item_arr_box'
      this.show_array_box = false
    }
    if (!this.mouse_status) return

    let all_list = JSON.parse(JSON.stringify(this.user_url_list))
    console.log(`item ==> `, item)
    let user_url_list
    if (this.group_id !== null) {

      user_url_list = JSON.parse(JSON.stringify(this.user_url_list[this.group_id]))
      console.log(`user_url_list ==> `, user_url_list)
      if (Math.abs(this.inner_offsetX) > 2 || Math.abs(this.inner_offsetY) > 2) {
        // 判断移动距离短
        // todo:组内移动到外面
        if (this.moveto_ind !== null) {
          let box = user_url_list.splice(this.inner_choosed_ind as number, 1)
          if (user_url_list.length === 1) {
            user_url_list = user_url_list[0]
          }
          all_list.splice(this.moveto_ind, 0, box[0])
          if (this.moveto_ind > this.group_id) {
            all_list[this.group_id] = user_url_list
          } else {
            all_list[this.group_id + 1] = user_url_list
          }

          this.user_url_list = all_list
          console.log(`all_list ==> `, all_list)

          this.moveto_ind = null
          this.choosed_ind = null

          this.offsetX = 0
          this.offsetY = 0

          this.group_id = null
          await this.$axios.post('/xz_server/url/setUrlList', {url_list: all_list})
        } else {
          // todo:组内移动
          if (this.inner_moveto_ind === null) {
            console.log(`this.moveto_ind ==> `, this.inner_moveto_ind)
            // this.jump(item, i)
          } else if (this.inner_choosed_ind !== this.inner_moveto_ind && this.inner_choosed_ind !== null) {
            let box = user_url_list.splice(this.inner_choosed_ind as number, 1)
            if (this.inner_moveto_ind < this.inner_choosed_ind) {
              // 往回移动
              user_url_list.splice(this.inner_moveto_ind, 0, box[0])
            } else {
              // 往外移动
              user_url_list.splice(this.inner_moveto_ind - 1, 0, box[0])
            }
            console.log(`user_url_list ==> `, user_url_list)
            this.user_url_list[this.group_id] = user_url_list
            await this.$axios.post('/xz_server/url/setUrlList', {url_list: user_url_list})
            // await this.$store.dispatch('user/get_user_url_list')
            // this.user_url_list = this.$store.state.user.user_url_list
          } else {
            console.log(111)
            // this.jump(item.url)
          }
        }

      } else {
        if (Array.isArray(item)) {
          this.group_id = i
          // this.$nextTick(() => {
          //   this.show_array_box = true
          // })
          setTimeout(() => {
            this.show_array_box = true
          })
          console.log(`this.group_id ==> `, this.group_id)
        } else {
          console.log('jump?')
          this.jump(item.url)
        }
      }
      this.inner_moveto_ind = null
      this.inner_choosed_ind = null

      this.inner_offsetX = 0
      this.inner_offsetY = 0
    } else {
      user_url_list = JSON.parse(JSON.stringify(this.user_url_list))

      if (Math.abs(this.offsetX) > 2 || Math.abs(this.offsetY) > 2) {
        // 判断移动距离短
        if (this.moveto_ind === null) {
          console.log(`this.moveto_ind ==> `, this.moveto_ind)
          // this.jump(item, i)
        } else if (this.choosed_ind !== this.moveto_ind && this.choosed_ind !== null) {
          let box = user_url_list.splice(this.choosed_ind as number, 1)
          if (this.moveto_ind < this.choosed_ind) {
            // 往回移动
            user_url_list.splice(this.moveto_ind, 0, box[0])
          } else {
            // 往外移动
            user_url_list.splice(this.moveto_ind - 1, 0, box[0])
          }
          this.user_url_list = user_url_list
          await this.$axios.post('/xz_server/url/setUrlList', {url_list: user_url_list})
          // await this.$store.dispatch('user/get_user_url_list')
          // this.user_url_list = this.$store.state.user.user_url_list
        } else {
          // this.jump(item.url)
        }
      } else {
        if (Array.isArray(item)) {
          this.group_id = i
          // this.$nextTick(() => {
          //   this.show_array_box = true
          // })
          setTimeout(() => {
            this.show_array_box = true
          })
          console.log(`this.group_id ==> `, this.group_id)
        } else {
          this.jump(item.url)
        }
      }
      this.moveto_ind = null
      this.choosed_ind = null

      this.offsetX = 0
      this.offsetY = 0
    }


    this.mouse_status = 0

    this.cancel_state()
  }

  cancel_state() {
    this.inner_moveto_ind = null
    this.inner_choosed_ind = null

    this.inner_offsetX = 0
    this.inner_offsetY = 0

    this.moveto_ind = null
    this.choosed_ind = null

    this.offsetX = 0
    this.offsetY = 0

    this.mouse_status = 0
  }

  calc_offsetX(i: number) {
    return i === this.choosed_ind ? this.offsetX : 0
  }

  calc_offsetY(i: number) {
    return i === this.choosed_ind ? this.offsetY : 0
  }

  calc_z_index(i: number) {
    return i === this.choosed_ind ? 1 : 2
  }

  inner_calc_offsetX(i: number) {
    return i === this.inner_choosed_ind ? this.inner_offsetX : 0
  }

  inner_calc_offsetY(i: number) {
    return i === this.inner_choosed_ind ? this.inner_offsetY : 0
  }

  inner_calc_z_index(i: number) {
    return i === this.inner_choosed_ind ? -1 : 2
  }

  hide_group() {
    if (this.show_add_dialog) return
    if (this.show_array_box) {
      this.group_id = null
      this.show_array_box = false

      this.cancel_state()
    }
  }

}
